import React from "react"
import BooksIcon from "./icons/booksIcon"
// import {
//   ReactIcon,
//   JavaScriptIcon,
//   HtmlIcon,
//   SassIcon,
//   ShopifyIcon,
// } from "../components/icons/logoIcons"

const Portfolio = props => {
  return (
    <div className="container">
      <div className="portfolio__container">
        <BooksIcon />
        <div className="portfolio__container--header">
          <h1>Work History</h1>
        </div>
        <div className="portfolio__container--content">
          <div className="timeline"></div>
          {props.content.allMarkdownRemark.edges.map(post => (
            <div key={post.node.id} className="portfolio__container--history">
              <div className="date">{post.node.frontmatter.date}</div>
              <div className="description">
                <h3>{post.node.frontmatter.title}</h3>
                <div dangerouslySetInnerHTML={{ __html: post.node.html }} />

              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  )
}

export default Portfolio
